html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*:before, *:after {
  box-sizing: inherit;
}

img,
embed,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

html,
body {
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

body {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  justify-content: space-between;
}

header {
  flex: 0;
}

main {
  flex: 1;
}

footer {
  flex: 0;
}

header {
  background-color: #333333;
}

main {
  background-color: #e9e1d9;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  font-family: DinPro, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  height: 30px;
  line-height: 30px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 21px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
}

.top-panel .left, .top-panel .right {
  flex: 1;
}

.top-panel .right {
  text-align: right;
}

.top-panel .center {
  flex: 1;
  display: flex;
  justify-content: inherit;
}

.top-panel .center .jackpot-item {
  display: flex;
}

.top-panel .center .jackpot-item .jackpot-name {
  margin-right: 11px;
}

.top-panel .center .jackpot-item .jackpot-counter {
  font-size: 17px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .row {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell {
  width: 19.5px;
  height: 30px;
  position: relative;
  transform: matrix(1, 0, 0, 1, 0, 0);
  transition: transform 1000ms ease-in-out;
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell + .jackpot-counter-cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.top-panel .center .jackpot-item .jackpot-counter .row .jackpot-counter-cell::before {
  position: absolute;
  content: '9 \a 0 \a 1 \a 2 \a 3 \a 4 \a 5 \a 6 \a 7 \a 8 \a 9 \a 1';
  line-height: 1.1;
  text-align: center;
  transform: matrix(1, 0, 0, 1, 0, -12);
  transition: transform 1000ms ease-in-out;
}

#container {
  width: 100%;
  height: 400px;
  -webkit-perspective: 800;
  /* For compatibility with iPhone 3.0, we leave off the units here */
  -webkit-perspective-origin: 50% 40%;
}

#stage {
  width: 100%;
  height: 100%;
  -webkit-transition: -webkit-transform 1s;
  -webkit-transform-style: preserve-3d;
  transform: translateZ(-20px);
}

#shape {
  /* display: flex;
      flex-direction: column; */
  position: relative;
  top: 160px;
  margin: 0 auto;
  height: 20px;
  width: 20px;
  -webkit-transform-style: preserve-3d;
}

.plane {
  height: 20px;
  width: 20px;
  text-align: center;
  font-size: 20px;
  line-height: 1;
  color: black;
  background-color: rgba(255, 250, 245, 0.4);
  -webkit-transition: -webkit-transform 2s, opacity 2s;
}

#shape {
  -webkit-animation: spin 8s infinite linear;
}

@-webkit-keyframes spin {
  from {
    -webkit-transform: rotateY(0);
  }
  to {
    -webkit-transform: rotateY(-360deg);
  }
}

.ring > .one {
  -webkit-transform: translateZ(31px);
}

.ring > .two {
  -webkit-transform: rotateY(36deg) translateZ(31px) translateY(-20px);
}

.ring > .three {
  -webkit-transform: rotateY(72deg) translateZ(31px) translateY(-40px);
}

.ring > .four {
  -webkit-transform: rotateY(108deg) translateZ(31px) translateY(-60px);
}

.ring > .five {
  -webkit-transform: rotateY(144deg) translateZ(31px) translateY(-80px);
}

.ring > .six {
  -webkit-transform: rotateY(180deg) translateZ(31px) translateY(-100px);
}

.ring > .seven {
  -webkit-transform: rotateY(216deg) translateZ(31px) translateY(-120px);
}

.ring > .eight {
  -webkit-transform: rotateY(252deg) translateZ(31px) translateY(-140px);
}

.ring > .nine {
  -webkit-transform: rotateY(288deg) translateZ(31px) translateY(-160px);
}

.ring > .ten {
  -webkit-transform: rotateY(324deg) translateZ(31px) translateY(-180px);
}

.ring > .eleven {
  -webkit-transform: rotateY(300deg) translateZ(31px) translateY(-40px);
}

.ring > .twelve {
  -webkit-transform: rotateY(330deg) translateZ(31px);
}
